<template>
  <div class="login">
    <h1>党团学新管理系统</h1>
  
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm">
      <el-form-item label="" prop="email">
        <el-input type="email" v-model="ruleForm.email" autocomplete="off" placeholder="请输入邮箱"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off" placeholder="请输入密码"></el-input>
      </el-form-item>
    </el-form>
    <el-button type="primary" @click="submitForm">登录</el-button>
  </div>
</template>

<script>
//import {sendLoginMsg} from '../../network/api'
export default {
  name: 'Login',
  data(){
    return{
      ruleForm:{
        email:'',
        password:''
      },
      rules:{
        email:[
                { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
              ],
        password:[
                { required: true, message: '请输入密码', trigger: 'blur' }
              ]
      }
    }
  },
  methods:{
    submitForm(){
      let data = this.ruleForm
      console.log(data);
      /*
      sendLoginMsg(data).then(res=>{
        console.log(res);
      })
      */
      this.$store.commit('changeLogin',{    //将token存到vuex中
          Authorization:'token'
      })
    }
  }
}
</script>

